import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ICartItem } from '@schemas/modules/cart/cart-item';

@Component({
    selector: 'tz-trip-timeline-dialog',
    templateUrl: './trip-timeline-dialog.component.html',
    styleUrls: ['./trip-timeline-dialog.component.scss'],
})
export class TripTimelineDialogComponent {
    public cartItems: ICartItem[] = [];

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private mdDialogRef: MatDialogRef<TripTimelineDialogComponent>
    ) {
        this.cartItems = data.cartItems;
    }

    public closeTimeline(): void {
        this.mdDialogRef.close();
    }
}
