import {
    ChangeDetectionStrategy,
    Component,
    Input,
    OnDestroy,
    OnInit,
    ChangeDetectorRef,
    ViewEncapsulation,
} from '@angular/core';
import { CartSummaryTimeline, TimelineServiceType } from '@schemas/modules/cart/cart-item-timeline';
import { TranslateService } from '@ngx-translate/core';
import { TzDatePipe } from '@shared/pipes/tz-date.pipe';
import { CSS_MAPPING } from '@core/constants/css-mapping';
import { ICartItem } from '@schemas/modules/cart/cart-item';
import { CartUtils } from '@services/modules/cart/utils/cart.utils';
import { SettingsService } from '@services/services/settings-service';
import { skip, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { ICartSelectedItemBookingOption } from '@schemas/modules/cart/cart-selected-item-booking-option';
import { CartStatus } from '@core/constants/cart-status';
import { PlatformService } from '@services/platform.service';

interface timelineData {
    id: string;
    serviceId: number;
    serviceStr: string;
    type: 'start' | 'end';
    timelineIcon: string;
    timelineLabel: string;
    timelineColor: string;
    title: string;
    time: number;
    curIdx: number;
    addLine: boolean;
    addDots: boolean;
    disabled: boolean;
}

const tripTrip = {
    flight: {
        start: 'TIMELINE.DEPARTURE',
        end: 'TIMELINE.ARRIVAL',
    },
    train: {
        start: 'TIMELINE.DEPARTURE',
        end: 'TIMELINE.ARRIVAL',
    },
    transfer: {
        start: 'TIMELINE.DEPARTURE',
        end: 'TIMELINE.ARRIVAL',
    },
    hotel: {
        start: 'HOTELS.CHECK_IN',
        end: 'HOTELS.CHECK_OUT',
    },
    rentcar: {
        start: 'CAR_RENTAL.PICK_UP_TIMELINE',
        end: 'CAR_RENTAL.DROP_OFF_TIMELINE',
    },
};

@Component({
    selector: 'tz-trip-timeline',
    templateUrl: './trip-timeline.component.html',
    styleUrls: ['./trip-timeline.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    providers: [TzDatePipe],
    encapsulation: ViewEncapsulation.None,
})
export class TripTimelineComponent implements OnInit, OnDestroy {
    @Input() cartItems: ICartItem[] = [];
    @Input() selectedId = '';

    public timelineList: timelineData[] = [];

    private destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(
        private translateService: TranslateService,
        private datePipe: TzDatePipe,
        private settingsService: SettingsService,
        private changeDetectorRef: ChangeDetectorRef,
        public platformService: PlatformService
    ) {}

    ngOnInit() {
        this.initializeTimelineComponent();
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.complete();
    }

    private getTimelineIcon(type: TimelineServiceType, tripType?: 'start' | 'end'): string {
        if (type === 'flight' && tripType) {
            return CSS_MAPPING[type].timelineIcons[tripType];
        }

        return CSS_MAPPING[type].icon;
    }

    private formTimelineLable(date: Date, type: string): string {
        const timeFormat = this.settingsService.locales.DATETIME_FORMATS.shortTime;
        const dateFormat = this.settingsService.locales.DATETIME_FORMATS.shortDateMonth;
        const dateStr = this.datePipe.transform(date, dateFormat);
        const timeStr = this.datePipe.transform(date, timeFormat);
        return `${dateStr} <span class="text-vertical-divider">|</span>
                ${timeStr} <span class="text-vertical-divider">|</span>
                ${this.translateService.instant(type)}`;
    }

    private hasValidBookingOptions(options: ICartSelectedItemBookingOption[]): boolean {
        return options.some(
            (option) =>
                option.status <= CartStatus.STATUS_CONFIRMED || option.status === CartStatus.STATUS_PRICE_CHANGED
        );
    }

    private formTimelineData(timeline: CartSummaryTimeline, type: 'start' | 'end'): timelineData {
        const date = new Date(type === 'start' ? timeline.startDate : timeline.endDate);
        return {
            id: timeline.hash,
            serviceId: timeline.serviceId,
            serviceStr: timeline.type,
            type,
            timelineIcon: this.getTimelineIcon(timeline.type, type),
            timelineLabel: this.formTimelineLable(date, tripTrip[timeline.type][type]),
            timelineColor: CSS_MAPPING[timeline.type].color,
            title: timeline.title
                ? timeline.title
                : this.translateService.instant('SERVICES.' + timeline.type.toUpperCase()),
            time: date.getTime(),
            disabled: !this.hasValidBookingOptions(timeline.bookingOptions),
        } as timelineData;
    }

    private formTimeline(): void {
        const timelineData = CartUtils.generateTimelineData(
            this.cartItems,
            true,
            this.translateService
        ) as CartSummaryTimeline[];
        if (!timelineData || timelineData.length === 0) {
            return;
        }

        const timelineList: timelineData[] = [];
        for (const timeline of timelineData) {
            timelineList.push({
                ...this.formTimelineData(timeline, 'start'),
                curIdx: timelineList.length,
            } as timelineData);

            timelineList.push({
                ...this.formTimelineData(timeline, 'end'),
                curIdx: timelineList.length,
            } as timelineData);
        }

        timelineList.sort((a, b) => {
            if (a.time === b.time) {
                if (a.type === b.type) {
                    let startDelta;
                    if (a.type === 'end') {
                        startDelta =
                            +new Date(timelineList[b.curIdx - 1].time) - +new Date(timelineList[a.curIdx - 1].time);
                    } else {
                        startDelta =
                            +new Date(timelineList[b.curIdx + 1].time) - +new Date(timelineList[a.curIdx + 1].time);
                    }
                    if (startDelta === 0) {
                        return a.type === 'end' ? a.serviceId - b.serviceId : b.serviceId - a.serviceId;
                    }
                    return startDelta;
                } else {
                    return a.type < b.type ? -1 : 1;
                }
            }
            return a.time - b.time;
        });

        const totalCount = timelineList.length;
        for (const [idx, timeLine] of timelineList.entries()) {
            const nxtIdx = idx + 1;
            const preIdx = idx - 1;

            if (timeLine.type === 'start' || (preIdx !== -1 && timeLine.id !== timelineList[preIdx].id)) {
                timelineList[idx].addLine = true;
            }

            if (nxtIdx < totalCount && (timeLine.id !== timelineList[nxtIdx].id || timeLine.type === 'end')) {
                timelineList[idx].addDots = true;
            }
        }

        this.timelineList = timelineList;
        this.changeDetectorRef.detectChanges();
    }

    private initializeTimelineComponent(): void {
        this.formTimeline();

        this.settingsService.selectedLanguageSubject.pipe(skip(1), takeUntil(this.destroy$)).subscribe(() => {
            this.formTimeline();
        });
    }
}
